
import React, {useState, useEffect} from 'react';
import Footer from './components/Footer';
import MyNavbar from './components/NavBar.js';
import Home from './components/Home';
import Project from './components/Project';
import { Routes, Route } from 'react-router-dom';
import {Container} from 'react-bootstrap';
import {motion} from 'framer-motion';

const bgStyle = {
  // minHeight: '100%',
  // height: '100vh',
  // overflow: 'auto',
  // backgroundSize:'cover',
  backgroundColor: '#F8DFDB',
}

const LoaderVariant = {
  initial: { y: "-100vh" },
  animate: {
    y: 0,
    transition: { duration: 2.3, type: "tween", ease: "linear" },
  },
};

const Loader = () => {
  return (

      <motion.div
       variant={{y:'-80vh', opacity:1}}
       animate={{y:0, opacity:0}}
       transition={{ duration: 1, delay:1 }}
       style={{minHeight:'100vh', width:'100vw',
       backgroundColor:"#fdf7da",
       textAlign:'center'}}>

          <h2 style={{color:'#632B30'}}>Let's Get Ready!</h2>

      </motion.div>

  );
};

function App() {
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    setTimeout(() => {setLoading(false)}, 2000)
  }, [])
  const bgColor = loading ? "#fdf7da" : '#F8DFDB';
  return (
    <Container fluid style={{backgroundColor:'#F8DFDB'}}>
      {!loading && <MyNavbar/>}
      <Routes>
        <Route path="/" element={loading ? <Loader /> : <Home />} />
        <Route path="/project" element={<Project />} />
      </Routes>
      <Footer />
    </Container>

  );
}

export default App;
